:root {
    --blue-LR: linear-gradient(to right, rgb(248, 132, 0), #994a01);
    --blue-RL: linear-gradient(to left, rgb(248, 132, 0), #994a01);
    
}
h1 {
    font-family: 'Courier New', Courier, monospace;
}

#profilePicture {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15rem;
    height: 15rem;
}
#cv-pexel {
    height: 65rem;
    width: 65rem;
    
}
#frontEndDeveloper {
    color: #F78301
}
#textOrangeUnderline {
    border-style: solid;
    border-color: orange;
    border-left-color: white;
    border-right-color: white;
    border-top-color: white;
}
.profileIcons {
    width: 2rem;
    height: 2rem;
}
.cv-layout {
    background: white;
    width: 65rem;
    position: absolute;
    left: 20rem;
    box-shadow: 0px 0px 100px 50px;
    
}
.cv-topDivContainer {
    position: relative;
    width: 100%;
    height: 20rem;
    overflow: hidden;
}
.cv-topDivName {
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 3rem;
    top: 2rem;
    left: 2rem;
    color: white;
    position: absolute;

}
.cv-topDivAdres {
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 20px;
    position: absolute;
    top: 1rem;
    right: 2rem;
}
.cv-body {
    display: flex;
    flex-direction: row;
    justify-content: center;
    
}

.cv-profileContainer {
    display: flex;
    flex-direction: column;
    
}
.cv-profileInfo {
    color: white;
    background-image: var(--blue-LR);
    width: 15rem;
    height: fit-content;
    padding: 1rem;
    margin: 1rem;
    box-shadow: black 10px 5px 20px 5px 
}
.profineInfolayout {
    display: flex;
    flex-direction: row;
}

.cv-skills {
    color: black;
    background: white;
    width: 15rem;
    height: fit-content;
    padding: 1rem;
    margin: 1rem;
    box-shadow: black 5px 2px 25px 1px 
}

.cv-mainContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 80rem;
    
}
.cv-mainSections {
    display: flex;
    flex-direction: column;
    color: black;
    height: fit-content;
    padding: 1rem;
    margin: 1rem;
    box-shadow: black 10px 5px 20px 5px
}